.list-comment {
  font-size: 0.7rem;
  margin-bottom: 0;
  margin-top: 10px;
}
.list-comment ul:before {
  height: 0;
}
.list-comment .item-content {
  padding-left: 0.5rem;
}
.list-comment .item-after {
  font-size: 0.55rem;
  color: #999;
  margin-right: 0.5rem;
  margin-top: 0.75rem;
  position: absolute;
  top: 0;
  right: 0.5rem;
}
.list-comment .item-media {
  position: relative;
  width: 2rem;
  height: 2rem;
  padding: 0 !important;
  margin-top: 0.5rem;
}
.list-comment .item-media img {
  width: 100%;
  height: 100%;
  border-radius: 1rem;
  overflow: hidden;
}
.list-comment .item-media .icon-vip {
  position: absolute;
  bottom: 0;
  right: 0;
  background-image: url(../assets/images/icon-vip.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 0.6rem;
  height: 0.6rem;
  display: block;
  z-index: 999;
}
.list-comment .item-text {
  font-size: 0.55rem;
  color: #999;
  margin-top: 0.15rem;
}
.list-comment .item-text .nick-name {
  color: #ff4d7f;
}
.list-comment .item-content {
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}



