
img {
  max-width: 100%;
}
.active-container {
  position: absolute;
  width: 100%;
  min-height: 100%;
  background-size: 100% auto;
  background-color: #fed23d;
  background-repeat: no-repeat;
  text-align: center;
}
.active-container .main {
  max-width: 750px;
  min-width: 320px;
  margin: 0 auto;
}
.active-container .from-group-block {
  margin-top: 20px;
}
.active-container .from-group {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 90%;
  margin: 0 auto 10px;
}
.active-container .from-group.last {
  margin-bottom: 0;
}
.active-container .form-input {
  border: 0;
  width: 100%;
  height: 40px;
  padding: 10px;
  display: block;
}
.active-container .btn-verifyCode {
  height: 40px;
  margin-left: 10px;
}



